body {
  margin: 0;
  background-color: hsl(255, 6%, 25%);
}
.top-pane {
  background-color: hsl(255, 6%, 25%);
}
.pane {
  height: 50vh;
  display: flex;
}
.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  background-color: hsl(255, 6%, 25%);
}
.editor-title {
  display: flex;
  justify-content: space-between;
  background-color: hsl(255, 6%, 13%);
  color: aliceblue;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
}
.CodeMirror {
  height: 100% !important;
}
.code-mirror-wrapper {
  flex-grow: 1;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  overflow: hidden;
}
.editor-container.collapsed {
  flex-grow: 0;
}
.expand-collapse-btn {
  margin-left: 0.5rem;
  background: none;
  border: none;
  color: white;
}
.file-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  background-color: hsl(255, 6%, 25%);
}
.file-container.collapsed {
  flex-grow: 0;
}
.file-container-title {
  display: flex;
  justify-content: space-between;
  background-color: hsl(255, 6%, 13%);
  color: aliceblue;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
}
.file-collapse-btn {
  margin-left: 0.5rem;
  background: none;
  border: none;
  color: white;
}
.live-view-title {
  display: flex;
  justify-content: space-between;
  background-color: hsl(255, 6%, 13%);
  color: aliceblue;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
}
.save-code {
  background-color: greenyellow;
  margin-top: 1rem;
  border: none;
  height: 2.5rem;
  font-weight: bold;
}
button.save-code:hover {
  background-color: green;
}
